import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { Link } from "gatsby";
import { colors, device, focusCss } from "@layouts/styleConfig";
import { Link as I18nLink, useTranslation } from "gatsby-plugin-react-i18next";
import { useTheme } from "styled-components";
import Locale from "./Locale";
import SwitchTheme from "./SwitchTheme";
import { MdSettings } from "react-icons/md";
import { useSetRecoilState } from "recoil";
import { modalOpenState } from "@state/index";

const Layout = styled.div`
  padding: 4px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 2px ${colors.grey};

  .mobile_right_area {
    display: flex;
    align-items: center;

    .setting {
      margin-right: 8px;
      display: flex;
      align-items: center;
      color: ${({ theme }) => theme.color};
    }
  }

  label.hamburger_menu {
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .laptop_menu {
    display: none;
  }

  .title {
    line-height: 16px;
    font-size: 16px;
    font-weight: 900;
    white-space: pre;
    color: ${({ theme }) => theme.color};
    text-decoration: none;
    :hover {
      cursor: pointer;
    }
  }

  input#hamburger_menu {
    display: none;
  }

  .mobile_menu_hamburger {
    display: block;
    background-color: ${({ theme }) => theme.hoverColor};
    position: relative;
    height: 4px;
    width: 30px;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;

    border-radius: 2px;
    margin: 3px 0;
  }

  .line1 {
    transform-origin: 0% 0%;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
  }
  .line2 {
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
  }
  .line3 {
    transform-origin: 0% 100%;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
  }
  input#hamburger_menu:checked ~ * .line1 {
    transform: rotate(45deg);
  }
  input#hamburger_menu:checked ~ * .line2 {
    transform: scale(0);
  }
  input#hamburger_menu:checked ~ * .line3 {
    transform: rotate(-45deg);
  }

  .mobile_menu_content {
    opacity: 0;
    background: ${({ theme }) => theme.bgColor};
    position: absolute;
    top: 40px;
    left: 0;
    height: calc(100vh - 40px);
    width: 100%;
    transition: 0.3s;
    z-index: -1;

    display: flex;
    flex-direction: column;
    padding: 24px;

    &.active {
      opacity: 0.9;
      z-index: 2;
    }

    > a {
      margin: 24px 0;
      color: ${({ theme }) => theme.color};
      font-size: 24px;
      text-decoration: none;
      border-bottom: 1px solid ${({ theme }) => theme.contentColor};
      text-align: center;

      :hover {
        margin-bottom: 23px;
        border-bottom: 2px solid ${({ theme }) => theme.color};
      }
    }

    > li {
      list-style: none;

      display: flex;
      justify-content: space-between;
      align-items: center;

      margin-bottom: 16px;
    }
  }

  /* input#hamburger_menu:checked ~ .mobile_menu_content {
    opacity: 0.9;
    z-index: 2;
  } */

  ul {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .right_area {
    display: none;
  }

  @media ${device.tablet} {
    height: 70px;
    padding: 0px 12px;

    .mobile_right_area,
    label.hamburger_menu,
    .mobile_menu_content {
      display: none;
    }

    .title,
    .contact {
      line-height: 20px;
      font-size: 20px;
      :hover {
        cursor: pointer;
      }

      ${focusCss}
    }

    .laptop_menu {
      display: block;

      display: flex;
      list-style: none;

      font-size: 20px;
      font-weight: 900;
      white-space: pre;
      color: ${({ theme }) => theme.color};

      > a {
        margin: 2px 16px;
        text-decoration: none;
        color: ${({ theme }) => theme.postColor};
        line-height: 30px;
        position: relative;

        :hover {
          cursor: pointer;
          color: ${({ theme }) => theme.hoverColor};
          /* margin-bottom: 0px; */
          transition: 0.3s;
          /* border-bottom: 2px solid ${({ theme }) => theme.hoverColor}; */
          ::after {
            position: absolute;
            content: "";
            width: 100%;
            left: 0;
            bottom: 0;
            height: 2px;
            background-color: ${({ theme }) => theme.hoverColor};
            transition: 0.3s;
          }
        }

        &.activePath {
          color: ${({ theme }) => theme.hoverColor};
          /* border-bottom: 2px solid ${({ theme }) => theme.hoverColor}; */
          /* margin-bottom: 0px; */

          ::after {
            position: absolute;
            content: "";
            width: 100%;
            left: 0;
            bottom: 0;
            height: 2px;
            background-color: ${({ theme }) => theme.hoverColor};
            transition: 0.3s;
          }
        }

        ${focusCss};
      }
    }

    .right_area {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
`;

type HeaderPropTypes = {
  path?: string;
};

export default function Header({ path }: HeaderPropTypes) {
  const [menuOpen, setMenuOpen] = useState(() => true);
  const toggleMenu = () => setMenuOpen((t) => !t);
  const [mobileType, setMobileType] = useState<string>("setting");

  const hamburgetMenuCheckRef = useRef(null);
  const theme = useTheme();

  const setModalOpen = useSetRecoilState(modalOpenState);
  const toggleModalOpen = () => setModalOpen((open) => !open);

  const { t } = useTranslation();

  useEffect(() => {
    const html = document.querySelector("body") as HTMLBodyElement;
    if (hamburgetMenuCheckRef.current) {
      menuOpen
        ? (html.style.overflow = "hidden")
        : (html.style.overflow = "visible");
    }
  }, [menuOpen]);

  useEffect(() => {
    setMenuOpen(false);
  }, [path, theme]);

  const mobileMenuContentDict: { [key: string]: React.ReactNode } = {
    routes: (
      <>
        <I18nLink to="/blog" activeClassName={"activePath"}>
          {t("blog")}
        </I18nLink>
        {/* <Link to="/change-log" activeClassName={"activePath"}>
          {t("change-log")}
        </Link> */}
        <I18nLink to="/project" activeClassName={"activePath"}>
          {t("project")}
        </I18nLink>
      </>
    ),
    setting: (
      <>
        <li>
          <p>{t("themeTitle")}：</p>
          <SwitchTheme />
        </li>
        <li>
          <p>{t("languageTitle")}：</p>
          <Locale />
        </li>
      </>
    ),
  };

  return (
    <Layout>
      <I18nLink to="/" className="title">{`Denny \nChang`}</I18nLink>
      <ul className="laptop_menu">
        <I18nLink to="/blog" activeClassName={"activePath"}>
          {t("blog")}
        </I18nLink>
        {/* <I18nLink to="/change-log" activeClassName={"activePath"}>
          {t("change-log")}
        </I18nLink> */}
        <I18nLink to="/project" activeClassName={"activePath"}>
          {t("project")}
        </I18nLink>
      </ul>

      <div className="right_area">
        <SwitchTheme />
        <Locale />

        <div
          className="contact laptop_menu"
          onClick={() => toggleModalOpen()}
          role="button"
          tabIndex={0}
          onKeyDown={(e) => {
            if (e.key === "Enter") toggleModalOpen();
            e.currentTarget.blur();
          }}
        >
          {t("contact")}
        </div>
      </div>

      <input
        type="checkbox"
        id="hamburger_menu"
        checked={menuOpen && mobileType === "routes"}
        onChange={() => {
          toggleMenu();
          setMobileType("routes");
        }}
        ref={hamburgetMenuCheckRef}
      />

      <div className="mobile_right_area">
        <div className="setting">
          <MdSettings
            fontSize={32}
            onClick={() => {
              toggleMenu();
              setMobileType("setting");
            }}
          />
        </div>
        <label className="hamburger_menu" htmlFor="hamburger_menu">
          <span className="mobile_menu_hamburger line1" />
          <span className="mobile_menu_hamburger line2" />
          <span className="mobile_menu_hamburger line3" />
        </label>
      </div>
      {menuOpen && (
        <ul className={`mobile_menu_content ${menuOpen ? "active" : ""}`}>
          {mobileMenuContentDict[mobileType]}
        </ul>
      )}
    </Layout>
  );
}
