import { useWindowSize } from "@tool/useWindowSize";
import React, { useEffect } from "react";
import styled, { useTheme } from "styled-components";

const BackgroundLayout = styled.div.withConfig({
  componentId: "BackgroundLayout",
})`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  user-select: none;
  z-index: -10;

  box-sizing: border-box;
  /* border: 1px solid red; */
`;

const numberBetween = (number: number) =>
  Math.floor(Math.random() * 2 * number + 1) - number;

const mathRandomFloor = (number: number, added: number = 0) =>
  Math.floor(Math.random() * number + 1) + added;

const PI = Math.PI;

export default function Background() {
  const { WIDTH, HEIGHT } = useWindowSize();

  const theme = useTheme();
  const currentTheme = theme?.type;
  const isDarkTheme = currentTheme === "dark";

  let starArray = Array.from(
    { length: 100 },
    (_, i) =>
      new Star(
        numberBetween(WIDTH / 2),
        numberBetween(HEIGHT / 2),
        WIDTH / 2,
        HEIGHT / 2
      )
  );

  let shootingStarsArray: ShootingStar[] = [];

  function createShootingStar() {
    if (Math.random() > 0.5) {
      shootingStarsArray.push(
        new ShootingStar(
          numberBetween(WIDTH / 2),
          numberBetween(HEIGHT / 2),
          WIDTH / 2,
          HEIGHT / 2
        )
      );
    }
  }

  // let cloudArray = Array.from(
  //   { length: 5 },
  //   (_, i) => new Cloud(numberBetween(WIDTH / 2), numberBetween(HEIGHT / 2))
  // );

  let bubbleArray = Array.from(
    { length: 100 },
    (_, i) =>
      new Bubble(
        numberBetween(WIDTH / 2),
        numberBetween(HEIGHT / 2),
        WIDTH / 2,
        HEIGHT
      )
  );

  useEffect(() => {
    const canvas = document.getElementById("canvas") as HTMLCanvasElement;
    canvas.width = WIDTH;
    canvas.height = HEIGHT;
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;
    let animationId: number,
      lastTime: number = 0,
      shootingStarTime: number = 0,
      shootingStarInterval: number = 3000,
      bubbleTimer: number = 0,
      bubbleInterval: number = mathRandomFloor(2, 4) * 500; // 泡泡出現的頻率

    function darkThemeAnimation(timeStamp: number = 0) {
      const deltaTime = timeStamp - lastTime;
      lastTime = timeStamp;

      ctx.clearRect(0, 0, WIDTH, HEIGHT);
      ctx.save();
      ctx.translate(WIDTH / 2, HEIGHT / 2);

      starArray = starArray.filter((star) => !star.disappear);

      starArray.forEach((star) => {
        star.draw(ctx);
        star.update(deltaTime);
      });

      if (shootingStarTime > shootingStarInterval) {
        createShootingStar();
        shootingStarTime = 0;
      } else {
        shootingStarTime += deltaTime;
      }

      shootingStarsArray
        .filter((star) => !star.isDead)
        .forEach((star) => {
          star.update();
          star.draw(ctx);
          // star.opacity += 0.01;
        });

      ctx.restore();
      animationId = requestAnimationFrame(darkThemeAnimation);
    }

    function lightThemeAnimation(timeStamp: number = 0) {
      const deltaTime = timeStamp - lastTime;
      lastTime = timeStamp;

      ctx.clearRect(0, 0, WIDTH, HEIGHT);
      ctx.save();
      ctx.translate(WIDTH / 2, HEIGHT / 2);

      if (bubbleTimer > bubbleInterval) {
        bubbleArray.push(
          new Bubble(numberBetween(WIDTH / 2), HEIGHT / 2, WIDTH / 2, HEIGHT)
        );
        bubbleTimer = 0;
      } else {
        bubbleTimer += deltaTime;
      }

      bubbleArray
        .filter((bubble) => !bubble.disappear)
        .forEach((bubble) => {
          bubble.draw(ctx);
          bubble.update(deltaTime);
        });

      ctx.restore();
      animationId = requestAnimationFrame(lightThemeAnimation);
    }

    if (isDarkTheme) {
      darkThemeAnimation();
    } else {
      lightThemeAnimation();
    }

    return () => {
      cancelAnimationFrame(animationId);
    };
  }, [WIDTH, HEIGHT, isDarkTheme]);

  return (
    <BackgroundLayout>
      <canvas id="canvas" />
    </BackgroundLayout>
  );
}

class Star {
  x: number;
  y: number;
  width: number;
  height: number;
  size: number;
  twinkleSpeed: number;
  disappear: boolean;
  twinkleInterval: number;
  twinkleTimer: number;
  movingSpeed: number;

  constructor(x: number, y: number, width: number, height: number) {
    this.x = x;
    this.y = y;
    this.size = Math.random() * 1 + 1;
    this.twinkleSpeed = mathRandomFloor(10) / 10;
    this.disappear = false;
    this.twinkleInterval = 1000;
    this.twinkleTimer = 0;
    this.movingSpeed = 0.05;
    this.width = width;
    this.height = height;
  }
  draw(ctx: CanvasRenderingContext2D): void {
    ctx.beginPath();
    ctx.fillStyle = `rgba(255, 255, 255, ${this.twinkleSpeed})`;
    ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2, true);
    ctx.fill();
  }
  update(deltaTime: number): void {
    if (this.twinkleTimer > this.twinkleInterval) {
      this.twinkleSpeed = Math.random() * 0.5 + 0.5;
      this.twinkleTimer = 0;
    } else {
      this.twinkleTimer += deltaTime;
    }
  }
}

class ShootingStar extends Star {
  x: number;
  y: number;
  width: number;
  height: number;
  opacity: number;
  vx: number;
  vy: number;
  length: number;
  speed: number;
  isMoving: boolean;
  isDying: boolean;
  isDead: boolean;

  constructor(x: number, y: number, width: number, height: number) {
    super(x, y, width, height);
    this.x = x;
    this.y = y;
    this.size = 1.5;
    this.length = mathRandomFloor(150, 50);
    this.opacity = 0.9;
    this.speed = mathRandomFloor(10, 1);
    this.vx = 1 * this.speed;
    this.vy = -1 * this.speed;
    this.isMoving = true;
    this.isDying = false;
    this.isDead = false;
    this.width = width;
    this.height = height;
  }

  draw(ctx: CanvasRenderingContext2D): void {
    super.draw(ctx);

    //trail
    ctx.beginPath();
    ctx.fillStyle = `rgba(255,255,255,${this.opacity})`;
    ctx.moveTo(this.x, this.y);
    ctx.lineTo(this.x + this.size / 2, this.y + this.size / 2);
    ctx.lineTo(this.x + this.length, this.y - this.length);
    ctx.closePath();
    ctx.fill();
  }

  update(): void {
    this.x -= this.vx;
    this.y -= this.vy;

    if (this.isMoving) {
      this.length *= 0.99;

      // this.isMoving = true;
      this.opacity += 0.01;
      if (this.opacity >= 1.0) {
        this.isMoving = false;
        this.isDying = true;
      }
    }

    if (this.isDying) {
      this.length *= 0.99;

      this.opacity -= 0.01;
      if (this.opacity <= 0) {
        this.isDying = false;
        this.isDead = true;
      }
    }
  }
}

class Bubble {
  x: number;
  y: number;
  canvasWidth: number;
  canvasHeight: number;
  size: number;
  disappear: boolean;
  randomRed: number;
  randomGreen: number;
  randomBlue: number;
  bubbleTimer: number;
  bubbleInterval: number;

  constructor(x: number, y: number, canvasWidth: number, canvasHeight: number) {
    this.x = x;
    this.y = y;
    this.canvasWidth = canvasWidth;
    this.canvasHeight = canvasHeight;
    this.size = Math.random() * 5 + 10;
    this.disappear = false;
    this.randomRed = Math.random() * 100 + 100;
    this.randomGreen = Math.random() * 100 + 100;
    this.randomBlue = Math.random() * 100 + 100;
    this.bubbleTimer = 0;
    this.bubbleInterval = 150;
  }

  draw(ctx: CanvasRenderingContext2D): void {
    ctx.beginPath();
    ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2, true);
    ctx.fillStyle = `rgba(
      ${this.randomRed}, 
      ${this.randomGreen},
      ${this.randomBlue},
      0.1)`;
    ctx.fill();
  }
  update(deltaTime: number): void {
    if (this.bubbleTimer > this.bubbleInterval) {
      this.y -= 1;
      if (Math.random() > 0.5) {
        this.x += 1;
      } else {
        this.x -= 1;
      }
      this.bubbleTimer = 0;
    } else {
      this.bubbleTimer += deltaTime;
    }

    if (this.y < -this.canvasHeight / 2) {
      this.disappear = true;
    }
  }
}
