import React, { useState, useEffect, Suspense } from "react";
import Header from "../Header";
import styled, { ThemeProvider, createGlobalStyle } from "styled-components";
import Modal from "../Modal";
import ContactForm from "../ContactForm";
import { device } from "@layouts/styleConfig";
import { lightTheme, darkTheme } from "@layouts/theme";
import Background from "@components/page/IndexPage/Background";
import { ClientSideLoading } from "./ClientSideLoading";
import { useRecoilValue } from "recoil";
import { themeState } from "@state/index";

const GlobalStyle = createGlobalStyle`
  body {
    background: ${({ theme }) => theme.bgColor};
  }
  html {
    scroll-behavior: smooth;
  }
`;

const Layout = styled.div.withConfig({ componentId: "Container_Layout" })`
  background: ${({ theme }) => theme.bgColor};
  color: ${({ theme }) => theme.color};
  transition: background-color 0.2s ease-in, color 0.2s ease-in;
  z-index: 1;
  position: relative;
  .main_body {
    min-height: calc(100vh - 40px);
    box-sizing: border-box;

    .flexCenter_layout {
      min-height: calc(100vh - 40px);
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  @media ${device.tablet} {
    .main-body {
      min-height: calc(100vh - 70px);

      .flexCenter_layout {
        min-height: calc(100vh - 70px);
      }
    }
  }
`;

type ContainerPropTypes = {
  children: React.ReactNode;
  location?: { pathname: string };
};

export default function Container({ children, location }: ContainerPropTypes) {
  const currentTheme = useRecoilValue(themeState);
  const isDarkTheme = currentTheme === "dark";

  const currentPath = location?.pathname ?? "";

  return (
    <Suspense fallback={<ClientSideLoading />}>
      <ThemeProvider theme={isDarkTheme ? darkTheme : lightTheme}>
        <GlobalStyle />
        <Layout>
          <Header path={currentPath} />
          <div className="main_body">
            {children} <Background />
          </div>
          <Modal render={<ContactForm />} />
        </Layout>
      </ThemeProvider>
    </Suspense>
  );
}
