import { device, focusCss } from "@layouts/styleConfig";
import { useOnClickOutside } from "@tool/useClickOutside";
import { useI18next, useTranslation } from "gatsby-plugin-react-i18next";
import React, { RefObject, useRef, useState } from "react";
import styled, { useTheme } from "styled-components";

type languageDictType = {
  zh: {
    en: string;
    zh: string;
  };
  en: {
    en: string;
    zh: string;
  };
};

type dictKey = {
  en: string;
  zh: string;
};

function getLocalName(lang: string, currentLanguage: string) {
  const languageDict: languageDictType = {
    en: {
      en: "En",
      zh: "Zh",
    },
    zh: {
      en: "英文",
      zh: "中文",
    },
  };
  return languageDict[currentLanguage as keyof languageDictType][
    lang as keyof dictKey
  ];
}

type LocaleLayoutPropTypes = {
  isDarkTheme: boolean;
  ref?: RefObject<HTMLElement>;
};

const LocaleLayout = styled.div.withConfig({
  componentId: "LocaleLayout",
})<LocaleLayoutPropTypes>`
  button.language_switch_btn,
  ul.language_menu {
    display: none;
  }

  div.mobile_switchLanguage {
    width: 100px;
    display: flex;
    justify-content: center;

    > button.mobile_switchLanguage_btn {
      width: 50px;
      outline: none;
      border: 1px solid ${({ theme }) => theme.overlayColor};
      padding: 5px;
      opacity: 0.5;
      color: ${({ theme }) => theme.color};
      background-color: ${({ theme }) => theme.bgColor};
      border-collapse: collapse;
      filter: brightness(0.9);
      transition: all 0.5s ease-in-out;
      &.active {
        opacity: 1;
        font-weight: 900;
        background-color: ${({ theme }) => theme.bgColor};
        filter: brightness(1.2);
      }
      :first-child {
        border-radius: 10px 0 0 10px;
        border-right: none;
      }
      :last-child {
        border-radius: 0 10px 10px 0;
      }
    }
  }

  @media ${device.tablet} {
    div.mobile_switchLanguage {
      display: none;
    }

    position: absolute;
    right: 95px;
    button.language_switch_btn {
      display: block;
      border: none;
      padding: 4px;
      color: ${({ theme }) => theme.color};
      background: ${({ theme }) => theme.switcherBgColor};
      border: 2px solid ${({ theme }) => theme.borderColor};
      border-radius: 15px;
      height: 28px;
      width: 44px;
      line-height: 100%;

      :hover {
        cursor: pointer;
      }

      ${focusCss}
      :focus {
        border-radius: 15px;
        outline-offset: 0;
        outline-width: 0.025rem;
      }
    }
    ul.language_menu {
      display: block;
      position: absolute;
      list-style: none;
      color: ${({ theme }) => theme.color};
      /* background: ${({ theme }) => theme.hintColor}; */
      background: transparent;
      filter: blur(10);
      border-color: ${({ theme }) => theme.switcherBgColor};
      width: 100%;
      font-size: 0.8rem;
      li {
        /* padding: 4px 8px; */
        width: 100%;
        line-height: 150%;
        text-align: center;
        transition: all 0.2s ease-in-out;

        :hover {
          border-bottom: 2px solid ${({ theme }) => theme.borderColor};
          cursor: pointer;
        }

        ${focusCss}
        :focus {
          outline-offset: 0;
        }
      }
    }
  }
`;

export default function Locale() {
  const { languages, language, changeLanguage } = useI18next();
  const [open, setOpen] = useState<boolean>(false);
  const langRef = useRef(null);
  const { t } = useTranslation();
  const toggleClose = () => setOpen(false);

  const theme = useTheme();
  const currentTheme = theme?.type;
  const isDarkTheme = currentTheme === "dark";

  useOnClickOutside(langRef, toggleClose);

  return (
    <LocaleLayout isDarkTheme={isDarkTheme} ref={langRef}>
      {/* desktop */}
      <button
        className="language_switch_btn"
        onClick={() => setOpen((open) => !open)}
      >
        {getLocalName(language, language)}
      </button>
      {open && (
        <ul className="language_menu">
          {languages
            .filter((lang) => lang !== language)
            .map((lang, index) => (
              <li
                key={lang}
                tabIndex={index}
                onClick={() => {
                  changeLanguage(lang);
                  setOpen(false);
                }}
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    changeLanguage(lang);
                    setOpen(false);
                  }
                }}
              >
                {getLocalName(lang, language)}
              </li>
            ))}
        </ul>
      )}

      {/* mobile btn */}
      <div className="mobile_switchLanguage">
        <button
          className={`mobile_switchLanguage_btn ${
            language === "en" ? "active" : ""
          }`}
          onClick={() => changeLanguage("en")}
        >
          {t("en")}
        </button>
        <button
          className={`mobile_switchLanguage_btn ${
            language === "zh" ? "active" : ""
          }`}
          onClick={() => changeLanguage("zh")}
        >
          {t("zh")}
        </button>
      </div>
    </LocaleLayout>
  );
}
