import React from "react";
import { colors, device, focusCss } from "@layouts/styleConfig";
import styled, { useTheme } from "styled-components";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { themeType } from "@tool/types";
import { useSetRecoilState } from "recoil";
import { themeState } from "@state/index";

type SwitchThemeLayoutPropTypes = {
  isDarkTheme: boolean;
};

const SwitchThemeLayout = styled.div.withConfig({
  componentId: "SwitchThemeLayout",
})<SwitchThemeLayoutPropTypes>`
  input#switchTheme,
  input#mobile_switchTheme,
  label.switchTheme,
  .background {
    display: none;
  }

  div.mobile_switchTheme {
    width: 100px;
    display: flex;
    justify-content: center;

    > button.mobile_switchTheme_btn {
      width: 50px;
      outline: none;
      border: 1px solid ${({ theme }) => theme.overlayColor};
      padding: 5px;
      opacity: 0.5;
      color: ${({ theme }) => theme.color};
      background-color: ${({ theme }) => theme.bgColor};
      border-collapse: collapse;
      filter: brightness(0.9);
      transition: all 0.5s ease-in-out;
      &.active {
        opacity: 1;
        font-weight: 900;
        background-color: ${({ theme }) => theme.bgColor};
        filter: brightness(1.2);
      }
      :first-child {
        border-radius: 10px 0 0 10px;
        border-right: none;
      }
      :last-child {
        border-radius: 0 10px 10px 0;
      }
    }
  }

  @media ${device.tablet} {
    div.mobile_switchTheme {
      display: none;
    }

    position: absolute;
    right: 200px;

    display: flex;
    align-items: center;

    input {
      display: none;
    }

    label.switchTheme {
      display: block;
      position: absolute;
      width: 40px;
      height: 24px;
      border-radius: 15px;
      box-sizing: border-box;
      z-index: 2;
      overflow: hidden;
      border: 2px solid ${({ theme }) => theme.borderColor};
      box-sizing: content-box;
      :hover {
        cursor: pointer;
      }

      ${focusCss}
      :focus {
        border-radius: 15px;
        outline-offset: 0;
        outline-width: 0.025rem;
      }

      span {
        display: block;
        position: absolute;
        top: 2px;
        left: ${({ isDarkTheme }) => (isDarkTheme ? "18px" : "2px")};
        width: 20px;
        height: 20px;
        border-radius: 50%;
        box-sizing: border-box;
        transition: all ease-in-out 0.3s;
        color: ${({ isDarkTheme }) => (isDarkTheme ? "#fff" : "#ff0")};
        background-color: ${({ isDarkTheme }) =>
          isDarkTheme ? "#fff" : "#ff0"};
        border: 2px solid
          ${({ isDarkTheme }) => (isDarkTheme ? "#e8e8ea" : "#f5e216")};

        &:before {
          content: ".";
          position: absolute;

          ${({ isDarkTheme }) =>
            isDarkTheme
              ? ` top: -8px;
                left: 0px;
                color: #e8e8eaaa;
                transform: scale(1.5);`
              : ` top: -10px;
                left: 20px;
                color: #fff;
                transform: scale(3.5);`}
        }
        &:after {
          content: ".";
          position: absolute;

          ${({ isDarkTheme }) =>
            isDarkTheme
              ? ` top: -16px;
                left: 3px;
                color: #e8e8ea;
                transform: scale(2.2);`
              : ` top: -14px;
                left: 24px;
                color: #fff;
                transform: scale(4);`}
        }
      }
    }

    .background {
      display: block;
      background: ${({ isDarkTheme }) => (isDarkTheme ? "#343434" : "skyblue")};
      width: 44px;
      position: absolute;
      height: 24px;
      z-index: 1;
      border-radius: 15px;

      &:before {
        content: ".";
        position: absolute;
        top: -3px;
        left: 4px;
        color: ${colors.white};
      }
      &:after {
        content: ".";
        position: absolute;
        top: 4px;
        left: 10px;
        color: ${colors.white};
      }
    }
  }
`;

type SwitchTypePropTypes = {
  // isDarkTheme: boolean;
  // toggleTheme: Function;
};

export default function SwitchTheme() {
  const theme = useTheme();
  const currentTheme = theme?.type;
  const isDarkTheme = currentTheme === "dark";

  const setTheme = useSetRecoilState(themeState);

  const toggleTheme = (type?: themeType | null) => {
    if (!type) {
      setTheme(isDarkTheme ? "light" : "dark");
      return;
    }
    setTheme(type);
  };

  const { t } = useTranslation();
  return (
    <SwitchThemeLayout isDarkTheme={isDarkTheme}>
      {/* desktop */}
      <input
        type="checkbox"
        id="switchTheme"
        checked={isDarkTheme}
        onChange={() => toggleTheme()}
      />
      <label
        className="switchTheme"
        htmlFor="switchTheme"
        tabIndex={0}
        onKeyDown={(e) => {
          if (e.key === "Enter") toggleTheme();
        }}
      >
        <span className="toggle" />
      </label>
      <div className="background" />

      {/* mobile btn */}
      <div className="mobile_switchTheme">
        <button
          className={`mobile_switchTheme_btn ${isDarkTheme ? "active" : ""}`}
          onClick={() => toggleTheme("dark")}
        >
          {t("dark")}
        </button>
        <button
          className={`mobile_switchTheme_btn ${isDarkTheme ? "" : "active"}`}
          onClick={() => toggleTheme("light")}
        >
          {t("light")}
        </button>
      </div>
    </SwitchThemeLayout>
  );
}
