import React, { useState } from "react";
import styled from "styled-components";
import { colors, device } from "@layouts/styleConfig";
import Loading from "../Loading";
import { useTranslation } from "gatsby-plugin-react-i18next";

const Layout = styled.form.withConfig({ componentId: "ContactForm_Layout" })`
  display: flex;
  flex-direction: column;
  justify-content: center;

  p {
    text-align: center;
    margin: 0;
    color: ${colors.black};
  }

  hr {
    width: 80%;
    border: 0.5px solid ${colors.grey};
  }

  .btn_area {
    text-align: end;
    margin-top: 8px;
  }

  button {
    color: ${colors.white};
    background-color: ${colors.blue};
    border: none;
    padding: 8px;
    width: 100%;
    border-radius: 8px;
    transition: 0.3s;

    :hover {
      cursor: pointer;
      filter: brightness(1.1);
    }
  }

  @media ${device.tablet} {
    button {
      padding: 4px 8px;
      width: max-content;
    }
  }
`;

type InputLayoutPropTypes = {
  hasError: boolean;
};

const InputLayout = styled.div<InputLayoutPropTypes>`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  margin: 8px 0;

  label {
    color: ${colors.darkGrey};
    font-size: 14px;
    margin-bottom: 4px;
  }

  input {
    font-size: 16px;
    line-height: 150%;
    border: 1px solid
      ${({ hasError }) => (hasError ? colors.red : colors.darkGrey)};
    border-radius: 4px;
    padding: 4px;
  }

  textarea {
    resize: none;
    width: 100%;
    height: 100px;
    box-sizing: border-box;
    font-size: 16px;
    line-height: 150%;
    border: 1px solid
      ${({ hasError }) => (hasError ? colors.red : colors.darkGrey)};
    border-radius: 4px;
    padding: 4px;
  }

  @media ${device.tablet} {
  }
`;

const DisplayResultLayout = styled.div.withConfig({
  componentId: "DisplayResult_Layout",
})`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 300px;
  color: ${colors.black};

  p {
    white-space: pre;
    text-align: center;
  }

  button {
    background-color: ${colors.blue};
    color: ${colors.white};
    border: none;
    padding: 4px 12px;
    border-radius: 4px;
    margin-top: 16px;
    :hover {
      cursor: pointer;
      filter: brightness(1.1);
    }
  }
`;

type ContactFormPropTypes = {
  onCancel?: Function;
  initialStatus?: string;
};

export default function ContactForm({
  onCancel = () => {},
  initialStatus = "initial",
}: ContactFormPropTypes) {
  const [errors, setErrors] = useState<{ [key: string]: boolean }>({});
  const [sendingStatus, setSendingStatus] = useState<string>(initialStatus);

  const { t } = useTranslation();

  const submitForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const nameValue = (document.getElementById("name") as HTMLInputElement)
      .value;

    const commentsValue = (
      document.getElementById("comments") as HTMLTextAreaElement
    ).value;

    if (!nameValue || !commentsValue) {
      setErrors({ name: !nameValue, comments: !commentsValue });
      return;
    }

    setSendingStatus("loading");

    const requestBody = JSON.stringify({
      user: nameValue,
      message: commentsValue,
    });

    fetch(`${process.env.GATSBY_MESSAGE_API}`, {
      method: "POST",
      body: requestBody,
    })
      .then(() => {
        setSendingStatus("success");
      })
      .catch(() => {
        setSendingStatus("error");
      });
  };

  type renderPropTypes = {
    [key: string]: React.ReactNode;
  };

  const renderLayout: renderPropTypes = {
    initial: (
      <Layout id="contact_form" onSubmit={submitForm}>
        <p>{t("contact-form-title")}</p>
        <hr />
        <Input
          id="name"
          label={t("contact-form-name")}
          hasError={errors["name"]}
          handleError={setErrors}
        />

        <Textarea
          id="comments"
          label={t("contact-form-messages")}
          hasError={errors["comments"]}
          handleError={setErrors}
        />

        <div className="btn_area">
          <button type="submit">{t("submit")}</button>
        </div>
      </Layout>
    ),
    loading: (
      <DisplayResultLayout>
        <Loading />
      </DisplayResultLayout>
    ),
    success: (
      <DisplayResultLayout>
        <p>{t("contact-form-success")}</p>
        <button onClick={() => onCancel()}>{t("close")}</button>
      </DisplayResultLayout>
    ),
    error: (
      <DisplayResultLayout>
        <p>{t("contact-form-errors")}</p>
      </DisplayResultLayout>
    ),
  };

  return <>{renderLayout[sendingStatus]}</>;
}

type InputPropTypes = {
  id: string;
  label: string;
  hasError: boolean;
  handleError: Function;
};

function Input({
  id,
  label,
  hasError,
  handleError = () => {},
}: InputPropTypes) {
  return (
    <InputLayout hasError={hasError}>
      <label htmlFor={id}>{label}</label>
      <input
        type="text"
        id={id}
        onFocus={() =>
          handleError((prev: { [key: string]: boolean }) => ({
            ...prev,
            [id]: false,
          }))
        }
      />
    </InputLayout>
  );
}

function Textarea({
  id,
  label,
  hasError,
  handleError = () => {},
}: InputPropTypes) {
  return (
    <InputLayout hasError={hasError}>
      <label htmlFor={id}>{label}</label>
      <textarea
        id={id}
        onFocus={() =>
          handleError((prev: { [key: string]: boolean }) => ({
            ...prev,
            [id]: false,
          }))
        }
      />
    </InputLayout>
  );
}
