import React, { useEffect } from "react";
import { colors, device } from "@layouts/styleConfig";
import styled from "styled-components";
import { useRecoilState } from "recoil";
import { modalOpenState } from "@state/index";

type ModalPropTypes = {
  render: React.ReactNode | null;
};

const Layout = styled.div.withConfig({ componentId: "Modal_Layout" })`
  .modal {
    position: fixed;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);

    box-sizing: border-box;
    border-radius: 16px;
    background-color: ${colors.white};
    z-index: 3;

    width: 70vw;
  }

  .overlay {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: ${({ theme }) => theme.overlayColor};
    z-index: 2;
    opacity: 0.6;
  }

  .modal_content {
    padding: 32px 16px 16px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }

  .btn {
    background-color: ${colors.blue};
    position: absolute;
    padding: 4px 4px;
    color: ${colors.white};
    transition: 0.3s;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    text-align: center;
    line-height: 14px;
    top: -12px;
    right: 12px;

    :hover {
      cursor: pointer;
      filter: brightness(1.1);
    }
  }

  @media ${device.tablet} {
    .modal {
      width: 50%;
      top: 50%;
    }

    .modal_content {
      padding: 32px 32px 16px;
    }
  }
`;

export default function Modal({ render }: ModalPropTypes) {
  const [modalOpen, setModalOpen] = useRecoilState(modalOpenState);
  const toggleModalOpen = () => setModalOpen((open) => !open);

  useEffect(() => {
    if (!modalOpen) return;

    function handleCloseModal(e: KeyboardEvent) {
      if (e.key === "Escape") {
        toggleModalOpen();
      }
    }
    document.addEventListener("keydown", handleCloseModal);

    return () => {
      document.removeEventListener("keydown", handleCloseModal);
    };
  }, [modalOpen]);

  useEffect(() => {
    const html = document.querySelector("html") as HTMLElement;
    modalOpen
      ? (html.style.overflow = "hidden")
      : (html.style.overflow = "visible");
  }, [modalOpen]);

  return (
    <>
      {modalOpen && (
        <Layout>
          <div className="modal">
            <div className="modal_content">{render}</div>
            <span
              role="button"
              tabIndex={0}
              className="btn close_btn "
              onClick={() => {
                toggleModalOpen();
              }}
              onKeyDown={(e) => {
                if (e.key === "Enter") toggleModalOpen();
              }}
            >
              x
            </span>
          </div>
          <div className="overlay" />
        </Layout>
      )}
    </>
  );
}
